.address {
  padding: 195px 0 175px;
  background-image: url("../images/bg.png");
  background-size: cover;


 &__title {
   margin-bottom: 100px;
   &::after {
     right: 25%;
     top: -300px;
     width: 538px;
     height: 271px;
     background-image: url("../images/icons/fish.svg");
   }
 }

  &__content {
    display: flex;
    justify-content: space-around;
    @include r($lg) {
     flex-direction: column;
      align-items: center;
    }

  }

  &__descr {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 500px;
    margin-right: 10px;
    @include r($lg) {
      max-width: 800px;
    }

  }

  &__text {
    margin-bottom: 50px;
    font-size: 25px;
    line-height: 1.5;
    @include r($xl) {
      font-size: 20px;
      line-height: 30px;

    }
  }

  &__map {
    width: 100%;
    max-width: 510px;
  }
  &__map-iframe {
    max-width: 510px;
    width: 100%;
    height: 450px;

  }
}